body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --body-bg-color: #e2dcff;
  --base-font-size: 18px;
  --header-font-family: adelle, serif;
  --paragraph-font-family: proxima-nova, serif;
  --header-line-height: 1.1;
  --header-margin-bottom: 1rem;
  --header-color: #25262b;
  --h1-font-size: 4.236rem;
  --h2-font-size: 2.618rem;
  --h2-font-weight: 400;
  --h3-font-size: 1.618rem;
  --h3-font-weight: 400;
  --h3-font-family: var(--paragraph-font-family);
  --paragraph-font-size: 1rem;
  --paragraph-font-weight: 400;
  --input-height: 3rem;
  --input-border-radius: 0.5rem;
  --input-width: 35vw;
  --input-bg-color: #6046ff;
  --input-box-shadow: 0 2rem 4rem 0 #8677ff;
  --input-font-size: var(--h2-font-size);
  --input-color: #ffffff;
  --input-border: none; }

body {
  background-color: #e2dcff;
  background-color: var(--body-bg-color);
  min-height: 100vh; }

#root {
  padding-top: 10vh; }

html {
  font-size: 18px;
  font-size: var(--base-font-size); }

h1,
h2 {
  font-family: adelle, serif;
  font-family: var(--header-font-family);
  line-height: 1.1;
  line-height: var(--header-line-height);
  margin-bottom: 1rem;
  margin-bottom: var(--header-margin-bottom);
  color: #25262b;
  color: var(--header-color); }

h1 {
  font-size: 4.236rem;
  font-size: var(--h1-font-size); }

h2 {
  font-size: 2.618rem;
  font-size: var(--h2-font-size);
  font-weight: 400;
  font-weight: var(--h2-font-weight); }

h3 {
  font-size: 1.618rem;
  font-size: var(--h3-font-size);
  font-weight: 400;
  font-weight: var(--h3-font-weight);
  font-family: proxima-nova, serif;
  font-family: var(--h3-font-family); }

.grid {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center; }

input {
  height: 3rem;
  height: var(--input-height);
  width: 35vw;
  width: var(--input-width);
  color: #ffffff;
  color: var(--input-color);
  background-color: #6046ff;
  background-color: var(--input-bg-color);
  box-shadow: 0 2rem 4rem 0 #8677ff;
  box-shadow: var(--input-box-shadow);
  font-size: 2.618rem;
  font-size: var(--input-font-size);
  text-align: center;
  border-radius: 0.5rem;
  border-radius: var(--input-border-radius);
  outline: none;
  border: none;
  border: var(--input-border); }

